import React, { Component } from 'react';
import {
    Row, Col, Card, CardBody, Spinner
} from 'reactstrap';
import School from './subcomponents/School';
import Skill from './subcomponents/Skill';
import './Education.css';

export class Education extends Component {
    displayName = Education.name;

    schools = [
        {
            "id": 1,
            "name": "NBI/Handelsakademin",
            "logo": "nbi.png",
            "major": "Systemutveckling",
            "degree": "YH-Utbildning",
            "startDate": "Sep 2020",
            "endDate": "Jun 2022",
            "location": "Göteborg, Sverige"
        },
        {
            "id": 2,
            "name": "Kungliga Tekn. Högskolan",
            "logo": "kth.png",
            "major": "Industriell miljöbioteknik",
            "degree": "Civilingenjörsprogrammet",
            "startDate": "Aug 2012",
            "endDate": "Dec 2016",
            "location": "Stockholm, Sverige"
        }
    ]

   skills = [
        {
            "Id": 1,
            "category": "Programmeringsspråk",
            "skillsList": [
                "C# (.NET, MVC)",
                "HTML",
                "CSS",
                "JavaScript",
                "Java",
                "SQL",
                "JSON",
                "XML"
            ]
        },
        {
            "Id": 2,
            "category": "Språk",
            "skillsList": [
                "Svenska",
                "Engelska",
                "Franska",
                "Marockanska"
            ]
        }

    ]

    render() {
  
            return (
                <div className="edu">
                    <div className="edu-section">
                        <h1 className="text-center">Utbildning</h1>
                        <hr></hr>
                        <Row>
                            {this.schools.map(school =>
                                <School key={school.id} school={school} />
                            )}
                        </Row>
                    </div>
                    <div className="edu-section">
                        <h1 className="text-center">Färdigheter</h1>
                        <hr></hr>
                        <Row>
                            <Col md={17}>
                                <Card className="skills-card">
                                    <CardBody>
                                        {this.skills.map(skill =>
                                            <Skill key={skill.id} skill={skill} />
                                        )}
                                    </CardBody>
                                </Card>
                            </Col>
                        </Row>
                    </div>
                </div>
            );
        }
    
}; 