import React, { Component } from 'react';
import {
    Row, Col
} from 'reactstrap';
import Job from './subcomponents/Job';


export class Jobs extends Component {
    displayName = Jobs.name


    jobs = [
  {
    "id": 1,
    "companyName": "Chalmers Tekniska Högskola",
    "logo": "Chalmers.png",
    "jobTitle": "Forskare",
    "startDate": "Jan 2018",
    "endDate": "Apr 2020",
    "location": "Göteborg, Sverige",
    "jobDuties": [
      "Arbetade i ett forskarteam i avdelningen för Systembiologi, där arbetet bestod av mesta dels genmanipulation av mikroorganismer."
    ]
  },
  {
    "id": 2,
    "companyName": "Vitrolife",
    "logo": "vitrolife.png",
    "jobTitle": "Kvalitetsingenjör",
    "startDate": "June 2017",
    "endDate": "Dec 2017",
    "location": "Görborg, Sverige",
    "jobDuties": [
      "Jobbade inom QC-avdelningen där både medicintekniska instumenter för IVF ändamål och spermakvalité kontrollerades"
    ]
  }
]


    
    render() {
        return (
                <div>
                    <Row>
                        {this.jobs.map(job =>
                            <Col md={12} key={job.id}>
                                <Job job={job} />
                            </Col>
                        )}
                    </Row>
                </div>
            );
        }

    
};